  import React, { useState } from 'react';
  import axios from 'axios';
  import './Login.css';
  import logoExpress from '../logoExpress.png'; //    
  import { useNavigate } from 'react-router-dom';
  import TextField from '@mui/material/TextField';
  import PassWordField from '@mui/material/TextField';
  import Grid from '@mui/material/Unstable_Grid2';
  import Button from '@mui/material/Button';

  const Login = ({ onLogin,setLogin }) => {
    const [idError, setidError] = useState('');
    const [passError, setpassError] = useState('');
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [loginError, setLoginError] = useState(null); //   
    const [idpwError, setIdpwError] = useState('');
    const history = useNavigate();

    const handleUsernameChange = (e) => {
      setUsername(e.target.value);
    };
    const handlePasswordChange = (e) => {
      setPassword(e.target.value);
    };

    const handleLogin = () => {

      
      const data = {
        username: username,
        password: password,
      };
    
      axios.post('/login', data, {
          headers: {
            'Content-Type': 'application/json',
          },
        })
        .then((response) => {
          console.log(response.data);
          //    
          const token = response.data.accessToken;
          console.log(token);
    
          if (token) {
            //      
            localStorage.setItem('token', token);
            //      .
            // setLogin(true); // :   true 
            setLogin(data);
            onLogin(true);
            history("/");
          } else {
            //       
            setLoginError(' .');
            setIdpwError('   !');
          }
        })
        .catch((error) => {
          console.error(error);
          setLoginError('   .');
        });
    };

    return (
      <div className="login-container">

      <h2>
        <img src={logoExpress} alt="" /> {/*      */}
      </h2>
    <form className="form-container">
      <label className="label">
          <TextField type="text" value={username} onChange={handleUsernameChange} label="" />
      </label>
      <label className="label">
          <TextField type="password" label="" value={password} onChange={handlePasswordChange} />
      </label>
      <Button variant="contained" type="button" onClick={handleLogin}>
        
      </Button>
      {loginError && <Grid color='red' className="error" fontFamily="GmarketSansMedium">{loginError}</Grid>}
    </form>
  </div>
    );
  };


  export default Login;
