  const [selectedTerminalId2, setSelectedTerminalId2] = useState(''); //     2
  const [selectedRoundTerminalId, setSelectedRoundTerminalId] = useState(''); //     
  const [selectedRoundTerminalId2, setSelectedRoundTerminalId2] = useState(''); //     2
  const navigate = useNavigate(); 
  const [BusInfo, setBusInfo] = useState({});
// 
  const handleParty = (event) => {
    setParty(event.target.value);
  };

  const handleSubmit = (event) => {

    event.preventDefault(); 
    if (transportType === 'bus') {
      //      
      navigate('/bus');
    } else if (transportType === 'train') {
      //      
      navigate('/train');
    }

    // TODO:      
  };
 
